<template>
  <div id='id' style="z-index: 1;position: absolute;width: 100%;">
    <div class="top" :class="scrolled||$route.path==='/Article'?'top2':''">
      <div class="topcontent">
        <div class="topcontenttitle">
          <!-- <i class="iconfont icon-siglyphleaf" style="font-size:30px;"></i>  -->
          Creative6</div>
        <div class="topcontentfucbtnbox">
          <div class="fucbtn" @click="$router.push('/Home')">
            <i class="iconfont icon-weibiaoti1"></i> 首页
          </div>
          <div class="fucbtn" @click="goto('https://github.com/Creative6');">
            <!-- <i class="iconfont icon-suoyoushangpin2"></i> 归档 -->
            <i class="iconfont icon-github"></i> Github
          </div>
        </div>
      </div>
    </div>
    <div>
      <router-view></router-view>
    </div>
    <div class="dingbu">
      <div @click="dingbu">
        <i class="iconfont icon-back"></i>
      </div>
      <!-- <div @click="dibu">
        <i class="iconfont icon-more"></i>
      </div> -->
    </div>
    <div style="float: left;text-align: center;width: 100%;margin-top: 20px;font-size: 13px;border-top: 1px dashed #ccc;padding: 14px;color:#777">
      <div>Copyright © 2017-2018 Flower-Creative6</div>
      <div style="margin-top:3px;">
        <i class="iconfont icon-github"></i>https://github.com/Creative6</div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'name',
    data() {
      return {
        scrolled: false
      }
    },
    methods: {
      goto(u) {
        window.open(u);
      },
      handleScroll() {
        this.scrolled = window.scrollY > 0;
      },
      dingbu() {
        // document.body.scrollTop = 0
        // document.documentElement.scrollTop = 0
        this.scrollToTop(300);
      },
      dibu() {
        document.body.scrollTop = 999999
        document.documentElement.scrollTop = 999999
      },
      scrollToTop(scrollDuration) {
        var cosParameter = window.scrollY / 2,
          scrollCount = 0,
          oldTimestamp = performance.now();

        function step(newTimestamp) {
          scrollCount += Math.PI / (scrollDuration / (newTimestamp - oldTimestamp));
          if (scrollCount >= Math.PI) window.scrollTo(0, 0);
          if (window.scrollY === 0) return;
          window.scrollTo(0, Math.round(cosParameter + cosParameter * Math.cos(scrollCount)));
          oldTimestamp = newTimestamp;
          window.requestAnimationFrame(step);
        }
        window.requestAnimationFrame(step);
      }
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
      if (this.$route.path === '/') {
        console.log('path=/')
        this.$router.push('/Home');
      }
    }
  }

</script>

<style scoped>
  .dingbu div:hover {
    background: #afafaf;
  }

  .dingbu div {
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    float: left;
    transition: 0.2s;
    border-radius: 50px;
    background: #ddd;
    margin-bottom: 10px;
    transform: rotate(90deg);
  }

  .dingbu {
    position: fixed;
    width: 50px;
    height: 100px;
    /* background: #d2d2d2; */
    color: #888;
    right: 85px;
    bottom: 85px;
    transition: 0.2s;
    cursor: pointer;
    border-radius: 2px;
  }

  .fucbtn:hover::after {
    left: 0%;
    width: 100%;
  }

  .fucbtn::after {
    content: "";
    width: 0;
    height: 2px;
    background: #fff;
    position: absolute;
    top: 80%;
    left: 50%;
    transition: .3s;
  }

  .fucbtn {
    height: 60px;
    line-height: 60px;
    float: left;
    width: 100px;
    transition: 0.3s;
    font-size: 14px;
    position: relative;
    text-align: center;
    cursor: pointer;
  }

  .topcontentfucbtnbox {
    float: right;
    margin-left: 100px;
  }

  .topcontenttitle {
    height: 60px;
    line-height: 60px;
    font-size: 30px;
    float: left;
  }

  .topcontent {
    width: 1000px;
    height: 60px;
    margin: 0 auto;
    color: #fff;
  }

  .top2 {
    background: rgb(30, 67, 103);
  }

  .top {
    position: fixed;
    width: 100%;
    height: 60px;
    z-index: 1;
    transition: 0.3s;
    top: 0px;
    left: 0px;
    font-weight: 300;
  }

</style>



